                height: 100%;
                background: rgba(37, 37, 37, 0.7);
                // opacity: 0.3;
            }
            .container {
                width: 100%;
                max-width: 1170px;
                margin: 0 auto;
                height: 100%;
                .gap {
                    height: 100%;
                    padding: 0 15px;
                    display: flex;
                    justify-content: center; // 좌우 정렬
                    align-items: center; 
                    // 타이틀
                    .title {
                       z-index: 2; 
                       position: relative;
                       text-align:center;
                       width: 307px;
                       user-select: none;
                        figure {
                            img{
                                width: 100%;
                            }
                        }
                        hr {
                            border-top: 1px solid #93e4cf;
                            border-bottom: 1px solid #93e4cf;
                            width: 100%;
                            height: 4px;
                            margin: 40px 0; // 글짜 위 아래 벌려주기.
