                        .slide-container {
                            width: 100%;
                            .slide-view {
                                width: 100%;
                                overflow: hidden;
                                // overflow: visible;
                                .slide-wrap { // 기차 5칸 : 우측에서 좌측으로 애니메이션 구현
                                    width: 25% * 5; // 125% 계산을 하려면 한칸 띄워야한다. 
                                    display: flex;
                                    animation: slide-ani 4s ease both /* forwards backwards */;
                                    animation: slide-ani 4s ease-in both /* forwards backwards */;
                                    animation: slide-ani 4s ease-out both /* forwards backwards */;
                                    animation: slide-ani 4s ease-in-out both /* forwards backwards */;
                                    animation: slide-ani 4s cubic-bezier(0.075, 0.82, 0.165, 1) both /* forwards backwards */;
                                    animation: slide-ani 4s cubic-bezier(0.175, 0.885, 0.32, 1.275) both /* forwards backwards */;
                                    animation: slide-ani 4s cubic-bezier(0, 0.5, 0.8, 1) both /* forwards backwards */;
                                    animation: slide-ani 4s cubic-bezier(1, 0, 0.8, 1) both /* forwards backwards */;
                                    .slide {
                                        width: 25%;
                                        .col-gap {
                                            width: auto; // 마진을 쓰려면 넓이 오토를 써야한다. 
                                            margin: 0 15px;
                                            .top-box {
                                                width: 100%;  
                                                //height: 322.172px; 
                                                position: relative;
                                                overflow: hidden;
                                                &::after {
                                                    z-index: 2;
                                                    content:'';
                                                    display: block;
                                                    position: absolute;
                                                    top: 0;
                                                    left: 0;
                                                    width: 100%;
                                                    height: 100%;
                                                    background: rgba(0,0,0,0);
                                                    cursor: pointer;
                                                    transition: all 0.3s;
                                                }
                                                img {
                                                    width: 100%;
                                                    position: relative;
                                                    z-index: 1;
                                                    transition: all 0.3s;             
                                                }   
                                                span {
                                                    z-index: 3;
                                                    position: absolute; // a4 용지 두장을 겹치게하는거 
                                                    right: 0;
                                                    bottom: 0;
                                                    padding: 15px 25px;
                                                    background: #fff;
                                                    em {
                                                        font-size: 13px;
                                                        color: #626262;
                                                        font-weight: 600;
                                                        text-transform: uppercase;
                                                    }
                                                } 
                                            }
                                            .bottom-box {
                                                width: 100%; 
                                                padding: 25px;
                                                background: #fff;
                                                display: flex;

                                                h3 {
                                                    font-size: 35px;
                                                    font-weight: 700;
                                                    color:#e15276;
                                                    margin: 0 25px 0 0;
                                                }
                                                div {
                                                    h4 {
                                                        font-size: 15px;
                                                        font-weight: 600;
                                                        color:#222;
                                                        text-transform: uppercase;
                                                    }
                                                    p {
                                                        font-size: 14px;
                                                        color: #626262;
                                                        line-height: 180%;
                                                        font-family: 'Roboto';
                                                        padding: 10px 0 30px 0; //위 오른쪽 아래 왼쪽 
                                                    }
                                                    i {
                                                        display: block;
                                                        width: 30px;
                                                        height: 4px;
                                                        background: #93e4cf;
                                                    }
                                                }
                                            }

                                            &:hover {
                                                .top-box {
                                                    &::after {
                                                        background: rgba(0,0,0, 0.3);
                                                    }
                                                }
                                                img {
                                                    transform: scale(1.2);
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        // 애니메이션 이름
        // slide-ani 
        // 3초대기 1초이동
        // 100% / 4 = 25% : 1초에 해당하는 백분율
        // 키프레임
        // 이동 트랜스폼 : x 이동 우측에서 좌측으로 이동 트랜스래이트 x( -475.75px)
        // 이동 transform :  translateX( -475.75px);
        @keyframes slide-ani {
             0% {transform: translateX(-475.75px * 0)}
            25% {transform: translateX(-475.75px * 0)}
            50% {transform: translateX(-475.75px * 0)}
            75% {transform: translateX(-475.75px * 0)}
           100% {transform: translateX(-475.75px * 1)}
        } 

        #section9 {
            width: 100%;
            height: 614.594px + 130px + 130px;
            background: #f7f7f7;
            padding: 130px 0;
        }
        #section10 {
            width: 100%;
            height: 294.375px + 130px + 80px;
            background: #fff;
            padding: 130px 0 80;
        }
        #section11 {
            width: 100%;
            height: 420px;
            background: #fff;
            iframe {
                width: 100%;
                height: 100%;
                border: 0;
            }
        }
    

    }
    #footer {   
        position: relative;
        z-index: 1;
        width: 100%;
        height: 300px;
        background:#dcdcdc; 
    }
 } 
